
import { getElem, getElems } from "../tools/utilities/helper-functions";
const SELECTOR_SLIDE = '[data-accordion-slide]';
const SELECTOR_TITLE = '[data-accordion-title]';
const EXPANDED_CLASS = 'is-expanded';

export const initAccordion = (container: HTMLElement) => {

  let slides = getElems(SELECTOR_SLIDE, container);

  onSlideClick((clickedSlide: Element) => {
    if(slideAlreadyOpened(clickedSlide)){
      expandSlide(clickedSlide, false)
    } else {
      expandSlide(clickedSlide)
      closeOtherSlides(clickedSlide)
    }
  })


  // Implementation

  function onSlideClick(action: (slide: Element) => void){
    slides.forEach(slide => {
      getElem(SELECTOR_TITLE, slide)!.addEventListener('click', (e: MouseEvent) => {
        let currentSlide = (e.target as HTMLElement).closest(SELECTOR_SLIDE)!;
        action(currentSlide)
      })
    })
  }
  
  function expandSlide(slide: Element, expand = true){
    const {classList} = slide
    expand ? classList.add(EXPANDED_CLASS) : classList.remove(EXPANDED_CLASS)
  }

  function closeOtherSlides(clickedSlide: Element){
    slides.forEach(slide => 
      slide !== clickedSlide && expandSlide(slide, false)
    )
  }

  function slideAlreadyOpened(clickedSlide: Element): boolean{
    return clickedSlide.classList.contains(EXPANDED_CLASS)
  }


}

