

import { elemIsInView, getElem, onReady, onScroll } from "../tools/utilities/helper-functions";
import {initAccordion} from '../objects/accordion';
const container = getElem('[data-product-info]')!;
const accordionContainer = getElem('[data-accordion-container]', container)!;
accordionContainer && initAccordion(accordionContainer);

const image = getElem('[data-image]', container);
if(!image) throw('image not found');
onScroll(() => {
  if(elemIsInView(image)){
    image.classList.add('slide-in');
  }
});
